import React, { useEffect, useState } from "react";
import Cookies from "js-cookie";
import { Link } from "react-router-dom";
import Header from "../Header/Header";
import "./Dashboard.css";
import * as XLSX from "xlsx";

const AdminDashboard = () => {
  const [userData, setUserData] = useState(null);
  const [editing, setEditing] = useState(false);
  const [children, setChildren] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [error, setError] = useState(null);
  const [games, setGames] = useState([]);
  const [editingChild, setEditingChild] = useState(null);

  const [formData, setFormData] = useState({
    firstName: "",
    lastName: "",
    middleName: "",
  });

  useEffect(() => {
    const fetchChildren = async () => {
      const token = Cookies.get("token");
      if (!token) {
        setError("  ");
        setLoading(false);
        return;
      }

      try {
        const response = await fetch("http://localhost:4200/users/me", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!response.ok) {
          throw new Error("    ");
        }

        const data = await response.json();
        setUserData(data);
        setFormData({
          username: data.username,
          lastName: data.lastName,
          firstName: data.firstName,
          middleName: data.middleName || "", // ,    
        });
      } catch (e) {
        setError(e.message);
      } finally {
        setLoading(false);
      }

      if (!token) {
        setError("  ");
        setLoading(false);
        return;
      }

      try {
        const response = await fetch("http://localhost:4200/users/children", {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!response.ok) {
          throw new Error("     ");
        }

        const data = await response.json();
        setChildren(data);
      } catch (e) {
        setError(e.message);
      } finally {
        setLoading(false);
      }
    };

    const fetchData = async () => {
      await fetchChildren(); //   
    };
    fetchData();
  }, []);
  const fetchUserGames = async () => {
    const token = Cookies.get("token");

    try {
      const response = await fetch("http://localhost:4200/users/game", {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!response.ok) {
        throw new Error("     ");
      }

      const data = await response.json();
      setGames(data);
    } catch (e) {
      setError(e.message);
    }
  };
  useEffect(() => {
    if (userData) {
      fetchUserGames(); //   
    }
  }, [userData]);
  const handleDelete = async (id) => {
    const token = Cookies.get("token");
    if (!token) {
      setError("  ");
      return;
    }
    if (window.confirm(" ,    ?"))
      try {
        const response = await fetch(
          `http://localhost:4200/users/children/${id}`,
          {
            method: "DELETE",
            headers: {
              Authorization: `Bearer ${token}`,
            },
          }
        );

        if (!response.ok) {
          throw new Error("   ");
        }

        //      
        setChildren(children.filter((child) => child.id !== id));
      } catch (e) {
        setError(e.message);
      }
  };

  const handleEdit = (child) => {
    setEditingChild(child);
    setFormData({
      firstName: child.firstName,
      lastName: child.lastName,
      middleName: child.middleName,
    });
  };

  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData({
      ...formData,
      [name]: name === "completed" ? e.target.checked : value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const token = Cookies.get("token");

    try {
      const response = await fetch(
        `http://localhost:4200/users/children/${editingChild.id}`,
        {
          method: "PUT",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`,
          },
          body: JSON.stringify(formData),
        }
      );

      if (!response.ok) {
        throw new Error("     ");
      }

      //   
      const updatedChild = await response.json();
      setChildren(updatedChild);
      setEditingChild(null);
    } catch (e) {
      setError(e.message);
    }
  };

  const handleCancel = () => {
    setEditingChild(null);
  };
  const handleEditToggle = () => {
    setEditing(!editing);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleUpdate = async (e) => {
    e.preventDefault();
    const token = Cookies.get("token");

    if (!token) {
      setError("  ");
      return;
    }

    try {
      const response = await fetch("http://localhost:4200/users/me", {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`,
        },
        body: JSON.stringify(formData),
      });

      if (!response.ok) {
        throw new Error("    ");
      }

      const updatedData = await response.json();
      setUserData((prevUserData) => ({
        ...prevUserData,
        ...updatedData,
      }));
      setEditing(false);
    } catch (e) {
      setError(e.message);
    }
  };
  const handleSearchChange = (event) => {
    setSearchTerm(event.target.value);
  };

  //    
  const filteredChildren = children.filter(
    (child) =>
      child.lastName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      child.firstName.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (child.middleName &&
        child.middleName.toLowerCase().includes(searchTerm.toLowerCase()))
  );
  const handleDeleteAccount = async () => {
    const token = Cookies.get("token");

    if (!token) {
      setError("  ");
      return;
    }

    if (window.confirm(" ,    ?")) {
      try {
        const response = await fetch("http://localhost:4200/users/me", {
          method: "DELETE",
          headers: {
            Authorization: `Bearer ${token}`,
          },
        });

        if (!response.ok) {
          throw new Error("   ");
        }

        alert("  ");
        //  
        Cookies.remove("token");
        //     (,  )
        window.location.href = "/login"; //    
      } catch (e) {
        setError(e.message);
      }
    }
  };
  function exportToXLSX() {
    const data = [];
    const headers = [
      "",
      "",
      "",
      " ",
      "",
      "",
    ];

    //  
    data.push(headers);

    filteredChildren.forEach((child) => {
      if (child.games.length > 0) {
        child.games.forEach((game, index) => {
          if (index === 0) {
            //       
            data.push([
              child.lastName,
              child.firstName,
              child.middleName,
              game.title || " ",
              game.score || 0,
              game.completed ? "" : "",
            ]);
          } else {
            //        
            data.push([
              "", // 
              "", // 
              "", // 
              game.title || " ",
              game.score || 0,
              game.completed ? "" : "",
            ]);
          }
        });
      } else {
        //     
        data.push([
          child.lastName,
          child.firstName,
          child.middleName,
          " ",
          "",
          "",
        ]);
      }
    });

    //    
    const ws = XLSX.utils.aoa_to_sheet(data);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, "");

    //   Excel
    XLSX.writeFile(wb, "report.xlsx");
  }

  //    

  if (loading) {
    return <div>...</div>;
  }

  if (error) {
    return <div style={{ color: "red" }}>{error}</div>;
  }

  return (
    <div>
      <Header />
      {userData && userData.role === "THERAPIST" ? (
        <div className="user-info">
          <h1>  </h1>
          {editing ? (
            <form onSubmit={handleUpdate} className="edit-form">
              <div className="form-field">
                <label>
                  :
                  <input
                    type="text"
                    name="username"
                    value={formData.username}
                    onChange={handleChange}
                    required
                  />
                </label>
              </div>
              <div className="form-field">
                <label>
                  :
                  <input
                    type="text"
                    name="lastName"
                    value={formData.lastName}
                    onChange={handleChange}
                    required
                  />
                </label>
              </div>
              <div className="form-field">
                <label>
                  :
                  <input
                    type="text"
                    name="firstName"
                    value={formData.firstName}
                    onChange={handleChange}
                    required
                  />
                </label>
              </div>
              <div className="form-field">
                <label>
                   ():
                  <input
                    type="text"
                    name="middleName"
                    value={formData.middleName}
                    onChange={handleChange}
                  />
                </label>
              </div>
              <button type="submit"></button>
              <button type="button" onClick={handleEditToggle}>
                
              </button>
            </form>
          ) : (
            <div>
              <p>: {userData.username}</p>
              <p>: {userData.lastName}</p>
              <p>: {userData.firstName}</p>
              <p>: {userData.middleName}</p>

              <button onClick={handleEditToggle}></button>
              <button onClick={handleDeleteAccount}> </button>
              <div>
                <Link to="/register">  </Link>
              </div>
              <input
                type="text"
                placeholder="  "
                value={searchTerm}
                onChange={handleSearchChange}
              />
            </div>
          )}
          <h3>:</h3>
          <table>
            <thead>
              <tr>
                <th></th>
                <th></th>
                <th></th>
                <th> </th>
                <th></th>
                <th></th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {filteredChildren.length > 0 ? (
                filteredChildren.map((child) =>
                  child.games.length > 0 ? (
                    child.games.map((game, index) => (
                      <tr key={game.id}>
                        {index === 0 && (
                          <>
                            <td rowSpan={child.games.length}>
                              {child.lastName}
                            </td>
                            <td rowSpan={child.games.length}>
                              {child.firstName}
                            </td>
                            <td rowSpan={child.games.length}>
                              {child.middleName}
                            </td>
                          </>
                        )}
                        <td>{game.title ?? " "}</td>
                        <td>{game.score ?? 0}</td>
                        <td>{game.completed ? "" : ""}</td>

                        {index === 0 && (
                          <td
                            rowSpan={child.games.length}
                            className="action-buttons"
                          >
                            <button onClick={() => handleEdit(child)}>
                              
                            </button>
                            <button onClick={() => handleDelete(child.id)}>
                              
                            </button>
                          </td>
                        )}
                      </tr>
                    ))
                  ) : (
                    <tr key={child.id}>
                      <td>{child.lastName}</td>
                      <td>{child.firstName}</td>
                      <td>{child.middleName}</td>
                      <td colSpan={3}> </td>
                      <td>
                        <button onClick={() => handleEdit(child)}>
                          
                        </button>
                        <button onClick={() => handleDelete(child.id)}>
                          
                        </button>
                      </td>
                    </tr>
                  )
                )
              ) : (
                <tr>
                  <td colSpan={7}>   </td>
                </tr>
              )}
            </tbody>
          </table>
          <button className="export" onClick={exportToXLSX}>
              XLSX
          </button>
          {editingChild && (
            <div>
              <h3> </h3>
              <form onSubmit={handleSubmit} className="edit-form">
                <div className="form-field">
                  <label>
                    :
                    <input
                      type="text"
                      name="firstName"
                      value={formData.firstName}
                      onChange={handleFormChange}
                      required
                    />
                  </label>
                </div>
                <div className="form-field">
                  <label>
                    :
                    <input
                      type="text"
                      name="lastName"
                      value={formData.lastName}
                      onChange={handleFormChange}
                      required
                    />
                  </label>
                </div>
                <div className="form-field">
                  <label>
                    :
                    <input
                      type="text"
                      name="middleName"
                      value={formData.middleName}
                      onChange={handleFormChange}
                    />
                  </label>
                </div>
                <button type="submit"></button>
                <button type="button" onClick={handleCancel}>
                  
                </button>
              </form>
            </div>
          )}
        </div>
      ) : (
        <div>
          {userData &&
            userData.role === "CHILD" && ( //     "child"
              <div className="user-info">
                <h1> </h1>
                {editing ? (
                  <form onSubmit={handleUpdate} className="edit-form">
                    <div className="form-field">
                      <label>
                        :
                        <input
                          type="text"
                          name="username"
                          value={formData.username}
                          onChange={handleChange}
                          required
                        />
                      </label>
                    </div>
                    <div className="form-field">
                      <label>
                        :
                        <input
                          type="text"
                          name="lastName"
                          value={formData.lastName}
                          onChange={handleChange}
                          required
                        />
                      </label>
                    </div>
                    <div className="form-field">
                      <label>
                        :
                        <input
                          type="text"
                          name="firstName"
                          value={formData.firstName}
                          onChange={handleChange}
                          required
                        />
                      </label>
                    </div>
                    <div className="form-field">
                      <label>
                         ():
                        <input
                          type="text"
                          name="middleName"
                          value={formData.middleName}
                          onChange={handleChange}
                        />
                      </label>
                    </div>
                    <button type="submit"></button>
                    <button type="button" onClick={handleEditToggle}>
                      
                    </button>
                  </form>
                ) : (
                  <div>
                    <p>: {userData.lastName}</p>
                    <p>: {userData.firstName}</p>
                    <p>: {userData.middleName}</p>
                    <div>
                      <h2> :</h2>
                      <ul>
                        {games.map((game) => (
                          <li key={game.id}>
                            <h3>{game.title}</h3>
                            <p>
                              :{" "}
                              {game.score !== null
                                ? game.score
                                : " "}
                            </p>
                            <p>: {game.completed ? "" : ""}</p>
                          </li>
                        ))}
                      </ul>
                    </div>
                    <button onClick={handleEditToggle}></button>
                    <button onClick={handleDeleteAccount}>
                       
                    </button>
                  </div>
                )}
              </div>
            )}
        </div>
      )}
    </div>
  );
};

export default AdminDashboard;
